<template>
    <div class="wraper" ref="wraper">
        <div class = "container">
            <div class="slider"><el-slider
             v-model="slider_value"
             vertical
             height="250px" >
            </el-slider></div>
            <span class="canvas-wraper">
                <canvas id="canvas" ref="canvas"></canvas>
            </span>
        </div>
        
      <div class="controlPanel_new">
        <!-- <div 
          :class="[initIdx == idx ? 'contro-item active' : 'contro-item']" 
          v-for="(item, idx) in toolsArr" :key="idx"
          @click="handleTools(item, idx)">
          <i :class="'iconfont' + item.icon"></i>
        </div> -->
        <div class = "color_palette">
            <el-button circle
                v-for="(item, idx) in color_btns"
                :key="idx"
                :style="{ background: item.color, width: '40px', height: '40px' }"
                @click="drawColor = item.color; fabricObj.freeDrawingBrush.color = item.color"
                ></el-button>
        </div>
        <div class = "func_panel">
            <span style="width: 10px"> </span>
            <button @click="handleTools('undo', 10)" style="background: none; border: none;"><img  :src="ArrowBackUp" style=" width: 50px; height: 50px ;margin-right: 10px"></button>
            <button @click="handleTools('pencil', 0)" style="background: none; border: none;"><img :src="PenAlt" style=" width: 50px; height: 50px; "></button>
            <button @click="handleTools('line', 1)" style="background: none; border: none;"><img :src="ZX" style=" width: 50px; height: 50px"></button>
            <button @click="handleTools('rectangle', 5)" style="background: none; border: none;"><img :src="JX" style=" width:50px; height: 50px"></button>
            <button @click="handleTools('ellipse', 7)" style="background: none; border: none;"><img :src="TY" style=" width: 50px; height: 50px"></button>
            <button @click="handleTools('text', 4)" style="background: none; border: none;"><img :src="WBK" style=" width: 50px; height: 50px"></button>
            <button @click="handleTools('remove', 9)" style="background: none; border: none;"><img :src="XPC" style=" width: 50px; height: 50px;margin-left: 10px"></button>
            <span style="width: 10px"> </span>
        </div>
        <!-- <button
          v-for="(item, idx) in toolsArr"
          @click="handleTools(item, idx)"
          :key="idx"
        >
          <i></i>
          {{ item.name }}
        </button> -->
        
      </div>
      <div class="download">
        
        <!-- <div></div>
        <button type="button" @click="decreaseWidth">-</button>
        <span>画笔粗细</span>
        <button type="button" @click="increaseWidth">+</button>
        <div></div> -->
        
        
            <el-button  :disabled="done" @click="downLoadImage">保存</el-button>
            <el-button  @click="generate">AI生成</el-button>   
            <el-button  @click="back">返回</el-button>
        
        <!-- <input type="range" id="width-change" @change="changeWidth"/> -->
        <!-- <input type="color" id="color-picker" @change="changeColor"/> -->
      </div>
      <div style="width:800px; "><img width="400px"   :src="imageBase64" v-show="imageBase64 != ''" alt="" class="img"></div>
    </div>
  </template>
  
<script>
import { fabric } from 'fabric'
import router from '../router'
import { post } from '../function/request.js'
import ArrowBackUp from '../assets/icon/ArrowBackUp.svg'
import PenAlt from '../assets/icon/PenAlt.svg'
import ZX from '../assets/icon/直线.svg'
import JX from '../assets/icon/矩形.svg'
import TY from '../assets/icon/椭圆.svg'
import WBK from '../assets/icon/文本框.svg'
import XPC from '../assets/icon/橡皮擦.svg'
export default {
    data() {
        return {
            ArrowBackUp: ArrowBackUp,
            PenAlt: PenAlt,
            ZX: ZX,
            JX: JX,
            TY: TY,
            WBK: WBK,
            XPC: XPC,
            slider_value: 0,
            id: null,
            currentTool: '',
            done: false,
            fabricObj: null,
            initIdx: 0,
            color_btns: [
                {
                    name: 'black',
                    color: '#000000'
                },
                {
                    name: 'pink',
                    color: '#ffc0cb'
                },
                {
                    name: 'light_purple',
                    color: '#d999ff'
                },
                {
                    name: 'purple',
                    color: '#800080'
                },
                {
                    name: 'red',
                    color: '#ff0000'
                },
                {
                    name: 'orange',
                    color: '#ffa500'
                },
                {
                    name: 'yellow',
                    color: '#ffff00'
                },
                {
                    name: 'light_green',
                    color: '#90ee90'
                },
                {
                    name: 'green',
                    color: '#008000'
                },
                {
                    name: 'blue',
                    color: '#0000ff'
                },

            ],
            toolsArr: [
                {
                    name: 'pencil',
                    icon: ' icon-pencil'
                },
                {
                    name: 'line',
                    icon: ' icon-line'
                },
                {
                    name: 'arrow',
                    icon: ' icon-arrow'
                },
                {
                    name: 'dashedline',
                    icon: ' icon-xuxian'
                },
                {
                    name: 'text',
                    icon: ' icon-ziti'
                },
                {
                    name: 'rectangle',
                    icon: ' icon-juxing'
                },
                {
                    name: 'circle',
                    icon: ' icon-yuanxing'
                },
                {
                    name: 'ellipse',
                    icon: ' icon-tuoyuanxing'
                },
                {
                    name: 'equilateral', //三角形
                    icon: ' icon-sanjiaoxing'
                },
                {
                    name: 'remove',
                    icon: ' icon-remove'
                },
                {
                    name: 'undo',
                    icon: ' icon-huitui'
                },
                {
                    name: 'redo',
                    icon: ' icon-xiangqian'
                },
                {
                    name: 'reset',
                    icon: ' icon-reset'
                },
            ],
            mouseFrom: {},
            mouseTo: {},
            moveCount: 1,
            doDrawing: false,
            fabricHistoryJson: [],
            mods: 0,
            drawingObject: null, //绘制对象
            drawColor: '#000000',
            drawWidth: 2,
            imageBase64: '',
            zoom: window.zoom ? window.zoom : 1,
        }
    },
    mounted() {
        //初始化canvas 
        this.initCanvas()
        this.id = this.$route.params.id
        console.log(this.id)
    },

    computed: {
        canvasWidth() {
            return window.innerWidth
        }
    },
    watch: {
        slider_value: function (val) {
            // this.zoom = val / 100
            // this.fabricObj.setZoom(this.zoom)
            // this.fabricObj.setWidth(this.canvasWidth)
            // this.fabricObj.setHeight(500)
            // this.fabricObj.renderAll()
            this.fabricObj.freeDrawingBrush.width = 2 + val / 5;
            this.drawWidth = 2 + val / 5;
        }
    },
    methods: {
        async generate() {
            // console.log('img generate img')
            // console.log(this.fabricObj.toDataURL().substring(22))
            this.$store.state.loading = true
            let res = await post(
                "/api/image_to_image",
                {
                    "text": localStorage.getItem('text'),
                    "user_image": this.fabricObj.toDataURL().substring(22)
                },
            );
            this.$store.state.loading = false
            localStorage.setItem('img', res.data.image)
            let imgbase64 = 'data:image/jpeg;base64,' + localStorage.getItem('img')
            this.imageBase64 = imgbase64
            this.done = false

            localStorage.setItem('draw_flag', '1')
        },
        back() {
            this.$store.state.state = 4
            this.$store.state.chatting_flag = 0
            this.$store.state.chatting_flag = 1
            this.$router.back()
        },
        initCanvas() {
            this.fabricObj = new fabric.Canvas('canvas', {
                isDrawingMode: true,
                selectable: false,
                selection: false,
                devicePixelRatio: true, //Retina 高清屏 屏幕支持
            })
            this.fabricObj.freeDrawingBrush.color = '#000000'
            this.fabricObj.freeDrawingBrush.width = 2
            this.fabricObj.setWidth(this.canvasWidth)
            this.fabricObj.setHeight(500)
            // this.fabricObj.add(
            //     new fabric.Rect({ top: 100, left: 100, width: 50, height: 50, fill: '#f55' }),
            //     new fabric.Circle({ top: 140, left: 230, radius: 75, fill: 'green' }),
            //     new fabric.Triangle({ top: 300, left: 210, width: 100, height: 100, fill: 'blue' }),
            // )
            //绑定画板事件
            this.fabricObjAddEvent()
        },
        //时间监听
        fabricObjAddEvent() {
            this.fabricObj.on({
                'mouse:down': (o) => {
                    this.mouseFrom.x = o.pointer.x;
                    this.mouseFrom.y = o.pointer.y;
                    this.doDrawing = true;
                    if (this.currentTool == 'text') {
                        this.drawText()
                    }
                },
                'mouse:up': (o) => {
                    this.mouseTo.x = o.pointer.x;
                    this.mouseTo.y = o.pointer.y;
                    this.drawingObject = null;
                    this.moveCount = 1;
                    this.doDrawing = false;
                    this.updateModifications(true);
                },
                'mouse:move': (o) => {
                    if (this.moveCount % 2 && !this.doDrawing) {
                        //减少绘制频率
                        return
                    }
                    this.moveCount++;
                    this.mouseTo.x = o.pointer.x;
                    this.mouseTo.y = o.pointer.y;
                    this.drawing();
                },
                //对象移动时间
                'object:moving': (e) => {
                    e.target.opacity = 0.5;
                },
                //增加对象
                'object:added': () => {
                    // debugger
                },
                'object:modified': (e) => {
                    e.target.opacity = 1;
                    // let object = e.target;
                    this.updateModifications(true)
                },
                'selection:created': (e) => {
                    if (e.target._objects) {
                        //多选删除
                        var etCount = e.target._objects.length;
                        for (var etindex = 0; etindex < etCount; etindex++) {
                            this.fabricObj.remove(e.target._objects[etindex]);
                        }
                    } else {
                        //单选删除
                        this.fabricObj.remove(e.target);
                    }
                    this.fabricObj.discardActiveObject(); //清楚选中框
                    this.updateModifications(true)
                },
            });
        },
        //储存历史记录
        updateModifications(savehistory) {
            if (savehistory == true) {
                this.fabricHistoryJson.push(JSON.stringify(this.fabricObj))
            }
        },
        //canvas 历史后退
        undo() {
            let state = this.fabricHistoryJson
            if (this.mods < state.length) {
                this.fabricObj.clear().renderAll();
                this.fabricObj.loadFromJSON(state[state.length - 1 - this.mods - 1]);
                this.fabricObj.renderAll();
                this.mods += 1;
            }
        },
        //前进
        redo() {
            let state = this.fabricHistoryJson
            if (this.mods > 0) {
                this.fabricObj.clear().renderAll();
                this.fabricObj.loadFromJSON(state[state.length - 1 - this.mods + 1]);
                this.fabricObj.renderAll();
                this.mods -= 1;
            }
        },
        transformMouse(mouseX, mouseY) {
            return { x: mouseX / this.zoom, y: mouseY / this.zoom };
        },
        resetObj() {
            this.fabricObj.selectable = false
            this.fabricObj.selection = false
            this.fabricObj.skipTargetFind = true
            //清除文字对象
            if (this.textboxObj) {
                this.textboxObj.exitEditing();
                this.textboxObj = null;
            }
        },
        handleTools(tools, idx) {
            console.log('handleTools')
            this.initIdx = idx;
            let t = {
                name: tools,
            }
            this.currentTool = t.name;
            this.fabricObj.isDrawingMode = false;
            this.resetObj()
            switch (t.name) {
                case 'pencil':
                    this.fabricObj.isDrawingMode = true;
                    break;
                case 'remove':
                    this.fabricObj.selection = true
                    this.fabricObj.skipTargetFind = false
                    this.fabricObj.selectable = true
                    break;
                case 'reset':
                    this.fabricObj.clear();
                    break;
                case 'redo':
                    this.redo();
                    break;
                case 'undo':
                    this.undo();
                    break;
                default:
                    break;
            }
        },
        //绘制文字对象
        drawText() {
            this.textboxObj = new fabric.Textbox(" ", {
                left: this.mouseFrom.x,
                top: this.mouseFrom.y,
                width: 220,
                fontSize: 18,
                fill: this.drawColor,
                hasControls: true
            });
            this.fabricObj.add(this.textboxObj);
            this.textboxObj.enterEditing();
            this.textboxObj.hiddenTextarea.focus();
            this.updateModifications(true)
        },
        drawing() {
            if (this.drawingObject) {
                this.fabricObj.remove(this.drawingObject)
            }
            let fabricObject = null
            switch (this.currentTool) {
                case 'pencil':
                    this.fabricObj.freeDrawingBrush.width = this.drawWidth
                    this.fabricObj.isDrawingMode = true
                    break;
                case 'line':
                    fabricObject = new fabric.Line([this.mouseFrom.x, this.mouseFrom.y, this.mouseTo.x, this.mouseTo.y], {
                        stroke: this.drawColor,
                        strokeWidth: this.drawWidth
                    })
                    break;
                case 'arrow':
                    fabricObject = new fabric.Path(this.drawArrow(this.mouseFrom.x, this.mouseFrom.y, this.mouseTo.x, this.mouseTo.y, 15.5, 15.5), {
                        stroke: this.drawColor,
                        fill: "rgba(255,255,255,0)",
                        strokeWidth: this.drawWidth
                    });
                    break;
                case 'dashedline':
                    // doshed line
                    fabricObject = this.drawDoshedLine()
                    break;
                case 'rectangle':
                    // 矩形
                    fabricObject = this.drawRectangle()
                    break;
                case "circle": //正圆
                    fabricObject = this.drawCircle()
                    break;
                case "ellipse":
                    // 椭圆
                    fabricObject = this.drawEllipse()
                    break;
                case "equilateral": //等边三角形
                    fabricObject = this.drawTriangle()
                    break;
                case 'remove':
                    break;
                default:
                    // statements_def'
                    break;
            }
            if (fabricObject) {
                this.$nextTick(() => {
                    this.fabricObj.add(fabricObject)
                    this.drawingObject = fabricObject
                })
            }
        },
        // dashed line
        drawDoshedLine() {
            return new fabric.Line([this.mouseFrom.x, this.mouseFrom.y, this.mouseTo.x, this.mouseTo.y], {
                strokeDashArray: [10, 3],
                stroke: this.drawColor,
                strokeWidth: this.drawWidth
            })
        },
        // circle
        drawCircle() {
            let radius = Math.sqrt((this.mouseTo.x - this.mouseFrom.x) * (this.mouseTo.x - this.mouseFrom.x) + (this.mouseTo.y - this.mouseFrom.y) * (this.mouseTo.y - this.mouseFrom.y)) / 2;
            return new fabric.Circle({
                left: this.mouseFrom.x,
                top: this.mouseFrom.y,
                stroke: this.drawColor,
                fill: "rgba(255, 255, 255, 0)",
                radius: radius,
                strokeWidth: this.drawWidth
            });
        },
        // triangle
        drawTriangle() {
            let height = this.mouseTo.y - this.mouseFrom.y
            return new fabric.Triangle({
                top: this.mouseFrom.y,
                left: this.mouseFrom.x,
                width: Math.sqrt(Math.pow(height, 2) + Math.pow(height / 2.0, 2)),
                height: height,
                stroke: this.drawColor,
                strokeWidth: this.drawWidth,
                fill: "rgba(255,255,255,0)"
            });
        },
        // ellipse
        drawEllipse() {
            let left = this.mouseFrom.x
            let top = this.mouseFrom.y
            // let ellipse = Math.sqrt((this.mouseTo.x - left) * (this.mouseTo.x - left) + (this.mouseTo.y - top) * (this.mouseTo.y - top)) / 2;
            return new fabric.Ellipse({
                left: left,
                top: top,
                stroke: this.drawColor,
                fill: "rgba(255, 255, 255, 0)",
                originX: "center",
                originY: "center",
                rx: Math.abs(left - this.mouseTo.x),
                ry: Math.abs(top - this.mouseTo.y),
                strokeWidth: this.drawWidth
            });
        },
        // rectangle
        drawRectangle() {
            return new fabric.Rect({
                left: this.mouseFrom.x,
                top: this.mouseFrom.y,
                width: this.mouseTo.x - this.mouseFrom.x,
                height: this.mouseTo.y - this.mouseFrom.y,
                fill: "rgba(255, 255, 255, 0)",
                stroke: this.drawColor,
                strokeWidth: this.drawWidth
            });
        },
        //书写箭头的方法
        drawArrow(fromX, fromY, toX, toY, theta, headlen) {
            theta = typeof theta != "undefined" ? theta : 30;
            headlen = typeof theta != "undefined" ? headlen : 10;
            // 计算各角度和对应的P2,P3坐标
            let angle = Math.atan2(fromY - toY, fromX - toX) * 180 / Math.PI,
                angle1 = (angle + theta) * Math.PI / 180,
                angle2 = (angle - theta) * Math.PI / 180,
                topX = headlen * Math.cos(angle1),
                topY = headlen * Math.sin(angle1),
                botX = headlen * Math.cos(angle2),
                botY = headlen * Math.sin(angle2);
            let arrowX = fromX - topX,
                arrowY = fromY - topY;
            let path = " M " + fromX + " " + fromY;
            path += " L " + toX + " " + toY;
            arrowX = toX + topX;
            arrowY = toY + topY;
            path += " M " + arrowX + " " + arrowY;
            path += " L " + toX + " " + toY;
            arrowX = toX + botX;
            arrowY = toY + botY;
            path += " L " + arrowX + " " + arrowY;
            return path;
        },
        async downLoadImage() {
            this.done = true
            //生成双倍像素比的图片
            let base64URl = this.fabricObj.toDataURL({
                formart: 'png',
                multiplier: 2
            })
            this.imageBase64 = base64URl
            this.done = false
            localStorage.setItem('img', this.fabricObj.toDataURL())
            localStorage.setItem('draw_flag', '1')
            // let res = await post(
            //     "/api/comment_image",
            //     {
            //         "base64_image": this.fabricObj.toDataURL().substring(22),
            //     },
            // );
            // console.log(res)
            // this.$store.state.img_comment = res.data.image_comment
            // this.$store.state.last_state = this.$store.state.state
            // this.$store.state.state = 3.9
            // this.$store.state.chatting_flag = 0
            // this.$store.state.chatting_flag = 1

        },
        increaseWidth() {
            this.fabricObj.freeDrawingBrush.width += 5;
            this.drawWidth += 5;
        },
        decreaseWidth() {
            this.fabricObj.freeDrawingBrush.width -= 5;
            this.drawWidth -= 5;
        },
        // changeWidth(evt)
        // {
        //   this.fabricObj.freeDrawingBrush.width = evt.srcElement.value;
        //   this.drawWidth = evt.srcElement.value;
        // },
        changeColor(evt) {
            this.drawColor = evt.srcElement.value;
            this.fabricObj.freeDrawingBrush.color = evt.srcElement.value;
        }
    },
}
</script>
  
<style lang="scss" scoped>
.color_palette {
    margin: auto;
    border-width: 2px;
    border-style: solid;
    border-color: #ebdbca;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    width: 550px;
    height: 60px;
    justify-content: center;
    align-items: center;
    display: flex;
    // background: linear-gradient(to right, #ebdbca, #ffffff);
    box-shadow: 0px 0px 10px 5px rgba(235, 219, 202, 0.5);
}

.func_panel {
    margin: auto;
    border-width: 2px;
    border-style: solid;
    border-color: #ebdbca;
    border-top-left-radius: 30px;
    border-top-right-radius: 30px;
    width: 650px;
    height: 80px;
    justify-content: center;
    justify-content: space-between;
    align-items: center;
    display: flex;
    // background: linear-gradient(to right, #ebdbca, #ffffff);
    box-shadow: 0px 0px 10px 5px rgba(235, 219, 202, 0.5);
}

.controlPanel_new {
    // display: flex;

    width: 100%;
    height: 50%;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: white;
}

// .func_panel {}



