    position: relative;
    width: 100%;
    height: 100%;
    // background-color: #fdfaf5;

    .container {
        height: 50%;
        width: 100%;
        margin-bottom: 10px;
        overflow: hidden;
        background-color: #fdfaf5;
        // background: url('../assets/001.jpg') repeat;
    }

    .slider {
        position: absolute;
        top: 100px;
        left: 15px;
        z-index: 100;
    }

    .canvas-wraper {
        // float: right;

        height: 40%;
        width: 100%;
        margin-bottom: 10px;
        overflow: hidden;
        // background: url('../assets/001.jpg') repeat;
        // background-color: #fdfaf5;
    }

    .controlPanel {
        width: 100%;
        height: 62px;
        background: #ddd;
        display: flex;
        justify-content: center;
        align-items: center;
        margin-bottom: 15px;

        .contro-item {
            flex-basis: 100px;
            border-right: 1px solid #dad7d9;
