                i {
                    font-size: 42px;
                }
            }
        }
    }

    .download {
        position: absolute;
        top: 20px;
        right: 0px;
        width: 30%;
        z-index: 100;
        // img {
        //     width: 800px;
        // }
    }
}
</style>