<template>
    <div class="chatting">
        <div class="ltk"></div>
        <div class="message" id="message">
            <Message v-for="item of messages" :data="item" :key="item.index" ></Message>
            <!-- <Message :data="m"></Message> -->
            <div style="height: 100px;" id="msg_end"></div>
        </div>
        <div class="ltk"></div>
        <div class="srk">
            <el-input placeholder="请输入内容"  v-model="input" class="input" autosize>
                <el-button slot="append" @click="voice" ><img :src="MKF" style="width: 20px"></el-button> 
                <el-button slot="append"  @click="send" ><img :src="FS" style="width: 20px"></el-button>
            </el-input>
                <!--    -->
        </div>
    </div>
</template>

<script>
import { post } from '../../function/request.js'
import Message from './Message.vue'
import FS from '../../assets/icon/发送.svg'
import MKF from '../../assets/icon/麦克风.svg'
export default {
    components: {
        Message
    },
    watch: {
        // "messages.length": {
        //     deep: true,
        //     handler: async function () {
        //         console.log('messageslength_watch:')
        //         if (this.messages.length > 0) {
        //             this.updateScroll()
        //         }
        //     },
        //     immediate: true
        // },
        "$store.state.chatting_flag": {
            deep: true,
            handler: async function () {
                // console.log('chatting_watch:' + this.$store.state.chatting_flag)
                if (this.$store.state.chatting_flag == 1) {
                    // console.log('chatting_state:' + this.$store.state.state)
                    switch (this.$store.state.state) {
                        case 1:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '那么这个主题都有哪些角色呢，可以选择手动或自动的方式添加',
                            })
                            this.messages.push({
                                self: false,
                                type: 2,
                            })
                            break;
                        case 1.1:

                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '你想添加的角色是什么呢',
                            })
                            break;
                        case 1.2:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '请在以下角色中选择',
                            })
                            // this.generate_object();
                            this.$store.state.state = 1.21;
                            this.$store.state.jsmind_flag = 0;
                            this.$store.state.jsmind_flag = 1;
                            break;
                        case 1.22:
                            this.messages.push({
                                self: false,
                                type: 1,
                                arr: this.$store.state.objects,
                            })
                            // this.$store.state.state = 1.21;
                            // this.$store.state.jsmind_flag = 0;
                            // this.$store.state.jsmind_flag = 1;
                            break;
                        case 2:
                            console.log('chatting_state: enter 2')
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '如果你喜欢可以为它添加一个声音',
                            })
                            this.messages.push({
                                self: false,
                                type: 3,
                            })
                            break;
                        case 2.11:
                            this.messages.push({
                                self: false,
                                type: 4,
                                content: '',
                            })
                            break;
                        case 3:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '如果你喜欢可以为它添加一个图像',
                            })
                            this.messages.push({
                                self: false,
                                type: 3,
                            })
                            break;
                        case 3.1:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '可以通过手动或自动的方式添加图像，你想选择哪一种呢',
                            })
                            this.messages.push({
                                self: false,
                                type: 2,
                                content: '',
                            })
                            break;
                        case 3.2: //手动
                            this.$router.push('/pad/' + this.$store.state.selected_node.id)
                            break;
                        case 3.3: //自动
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '下面请告诉我该角色应该是什么样子吧'
                            });
                            // this.$store.state.jsmind_flag = 0;
                            // this.$store.state.jsmind_flag = 1;
                            break;
                        case 3.31:
                            let imgbase64 = localStorage.getItem('img')
                            if (!imgbase64.startsWith("data")) {
                                imgbase64 = 'data:image/jpeg;base64,' + imgbase64
                            }
                            this.messages.push({
                                self: false,
                                type: 5,
                                content: '',
                                img: imgbase64,
                            })
                            this.messages.push({
                                self: false,
                                type: 4,
                            })
                            break;
                        case 3.9: //评价图画
                            console.log("3.9")
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: this.$store.state.img_comment,
                            })
                            this.$store.state.state = this.$store.state.last_state
                        case 4:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '如果你喜欢可以为它添加一个行为逻辑',
                            })
                            this.messages.push({
                                self: false,
                                type: 3,
                            })
                            break;
                        case 4.1:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '请选择手动或自动的方式添加逻辑',
                            })
                            this.messages.push({
                                self: false,
                                type: 2,
                            })
                            break;
                        case 4.2: //手动
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '它会有哪些动作或者与别的角色、物体有关联吗？',
                            })
                            break;
                        case 4.3: //自动
                            this.$store.state.jsmind_flag = 0;
                            this.$store.state.jsmind_flag = 1;
                            break;
                        case 4.31:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: localStorage.getItem('logic'),
                            })
                            this.messages.push({
                                self: false,
                                type: 4,
                            })
                            break;
                        case 5:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '你觉得这个行为还需要细化吗，如果没有那我们开始编程吧',
                            })
                            this.messages.push({
                                self: false,
                                type: 7,
                            })
                            break;
                        case 5.1:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '请选择手动或自动添加代码',
                            })
                            this.messages.push({
                                self: false,
                                type: 2,
                            })
                            break;
                        case 5.2: //手动
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '请手动添加代码',
                            })
                            break;
                        case 5.3: //自动
                            this.$store.state.jsmind_flag = 0;
                            this.$store.state.jsmind_flag = 1;
                            break;
                        case 5.31:
                            this.messages.push({
                                self: false,
                                type: 6,
                                arr: this.$store.state.codes,
                            })
                            this.messages.push({
                                self: false,
                                type: 4,
                            })
                            break;
                        case 6: //在逻辑后添加逻辑
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '请通过手动或自动的方式添加逻辑吧',
                            })
                            this.messages.push({
                                self: false,
                                type: 2,
                            })
                            break;
                        case 6.1: //手动
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: '请告诉我你对逻辑的描述吧，我会为你进行润色',
                            })
                            break;
                        case 6.21:
                            this.messages.push({
                                self: false,
                                type: 0,
                                content: this.$store.state.logic,
                            })
                            this.messages.push({
                                self: false,
                                type: 4,
                            })
                            break;

                    }
                    // this.updateScroll()
                }

                this.$store.state.chatting_flag = 0
            },
            immediate: true
        },
        "this.$store.state.hyp": {
            deep: true,
            handler: async function () {

                // console.log('hyp_watch:' + this.$store.state.hyp)
                // if (this.$store.state.hyp == 1) {
                //     switch (this.$store.state.state) {
                //         case 1.2:
                //             this.generate_object()
                //             break;
                //     }

                // }
                // this.$store.state.hyp = 0
            },
            immediate: true
        },
    },

    mounted() {
        var element = document.getElementById("message");
        this.$store.state.element = element

        // this.messages.push({
        //     self: false,
        //     type: 1,
        //     content: '你好3',
        //     arr: ['rabit', 'dog', 'cat'],
        // })
    },

    data() {
        return {
            FS: FS,
            MKF: MKF,
            input: '',
            state: 0,
            messages: [{
                self: false,
                type: 0,
                content: '我是小思老师。我会和你一起做一个思维导图来学习编程。你想创作什么故事呢',
            }],
        }
    },
    methods: {
        updateScroll() {
            // console.log('updateScroll')

            // var element = document.getElementById("msg_end");
            // console.log(element)
            // element.scrollIntoViewIfNeeded()
            // element.scrollByLines(10000)
            // element.scrollTop = element.scrollHeight;
            // element.scroll({
            //     top: 10000,
            //     behavior: "smooth"
            // });

            // element.scrollTo(0, 100000)
            // this.$nextTick(() => {
            //     this.$refs.message.scrollTop = this.$refs.message.scrollHeight
            // })
        },
        activated() {
            console.log('chatting activated')
        },
        async generate_object() {
            this.$store.state.loading = true
            let res = await post(
                "/api/object_generation",
                {
                    "user_input": cont,
                },
            );
            this.$store.state.loading = false
            let a
            // this.messages.pop()
            this.messages.push({
                self: false,
                type: 1,
                content: '',
                arr: a,
            })
        },
        voice() {
            const reg = new window.webkitSpeechRecognition;
            // const reg = new window.SpeechRecognitionResult()
            reg.lang = 'zh-CN';
            console.log('voice')
            const that = this
            reg.onresult = function (event) {
                const content = event.results[0][0].transcript;
                console.log(`你说的是：${content}`);
                that.input += content;
            };
            reg.start()
            reg.end(3000)
        },
        async send() {
            let cont = this.input;
            this.messages.push({
                self: true,
                type: 0,
                content: cont,
            })
            if (this.$store.state.state == 0) {
                this.determine_topic(cont)
                this.$store.state.state = 1;
                // this.$store.state.chatting_flag = 0;
                // this.$store.state.chatting_flag = 1;
            } else if (this.$store.state.state == 1.1) {
                this.$store.state.state = 1.11;
                this.$store.state.object = cont;
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;

            } else if (this.$store.state.state == 3.3) {
                this.$store.state.img_desc = cont;
                this.$store.state.state = 3.4;
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;
            } else if (this.$store.state.state == 4.2) {
                this.$store.state.logic = cont;
                this.$store.state.state = 4.21;
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;
            } else if (this.$store.state.state = 5.2) {
                this.$store.state.code = cont;
                this.$store.state.state = 5.21;
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;
            } else if (this.$store.state.state == 6.1) {
                this.$store.state.logic = cont;
                this.$store.state.state = 6.11;
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;
            }
            this.updateScroll()
            this.input = ''
        },
        addLineBreaks(str) {
            let result = '';
            for (let i = 0; i < str.length; i += 25) {
                if (i + 25 <= str.length)
                    result += str.slice(i, i + 25) + '\<br\>';
                else
                    result += str.slice(i, str.length);
            }
            return result;
        },
        async determine_topic(cont) {
            this.$store.state.loading = true
            let res = await post(
                "/api/extracted_subject",
                {
                    "user_input": cont,
                },
            );
            this.$store.state.loading = false
            console.log(res)
            let r = this.addLineBreaks(res.data.subject)
            this.$store.state.jm.update_node(this.$store.state.root.id, r)
        },
    }
}
</script>

<style lang="less" scoped>
