    overflow-y: scroll;
    background-color: #f5e4cd;
}

.ltk {
    height: 2%;
    width: 100%;
    text-align: center;
    // border: 1px solid #dee0e3;
    border: none;
    background-color: #fcd36f;
}

.input {
    width: 100%;
    height: 6%;

}

.srk {
    width: 100%;
    // border: 1px solid #dee0e3;
    border: none;
    position: absolute;
    bottom: 0px;
    background-color: #fcd36f;
