    // background-color: #fff;
    background-color: #eef1f8;
}

.m-chat-wrap {
    overflow: hidden;
    box-sizing: content-box;
    display: flex;
    flex-direction: column;

    // height: 100vh;
    // padding: 5vw 0px 5vw 0px;
    .m-chat-msg-wrap {
        display: block;
        overflow: hidden;
        box-sizing: content-box;
    }
}
</style>

