export const ping = async() => {
    console.log("ping");
    console.log(await request.get("http://127.0.0.1:5500/hello"));
    // 10.73.3.223
    // 127.0.0.1
    // http://10.73.3.223:55231/hello
};

export const post = async(url, data) => {
    try {
        // let t = "http://127.0.0.1:55001" + url //http://10.73.3.223:55231/
        let t = "http://10.73.3.223:55231" + url
        console.log("post", t, data);
        const response = await request.post(t, data);
        // const ret = response.data;
        return response;
    } catch (error) {
        if (axios.isAxiosError(error)) {
            if (error.response) {
                // 请求成功发出且服务器也响应了状态码，但状态代码超出了 2xx 的范围
                console.log(error.response.data);
                console.log(error.response.status);
                console.log(error.response.headers);
            } else if (error.request) {
                // 请求已经成功发起，但没有收到响应
                // `error.request` 在浏览器中是 XMLHttpRequest 的实例，
                // 而在node.js中是 http.ClientRequest 的实例
                console.log(error.request);
            } else {
                // 发送请求时出了点问题
                console.log("Error", error.message);
            }
            console.log(error.config);
        }
        throw error;
    }
}