<template>
    
    <div class="all">
        
        <div v-if="data.self == true" class="self">
                <el-button  style="background-color: #6ca1a6;"><div class="overflow">{{ data.content }}</div></el-button>
            <span>
                <el-avatar :src="ET" style="background-color: #b2c0dc;"></el-avatar>
                <!-- <el-button circle><img :src="ET"></el-button> -->
            </span>
            
        </div>

        <div v-if="data.self == false" class="gpt">
            <div v-if="data.type == 0">
                <el-row>
                <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <!-- @click="self_click" -->
                <el-button style="background-color: #d4cec9;"><div class="overflow" >{{ data.content }} </div></el-button>
                </el-row>
            </div>  
            <div v-if="data.type == 1">
                <el-row>
                    <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <el-button type="primary" plain v-for="item of data.arr" :key="item.index"  @click="addObj(item)"><div class="overflow"> {{ item }} </div></el-button>
                <el-button type="primary" plain @click="hyp"> 换一批 </el-button>
                </el-row>
            </div>
            <div v-if="data.type == 2">
                <el-row>
                    <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <el-button  type="primary" plain @click="sd"> 手动 </el-button>
                <el-button type="primary" plain @click="zd"> 自动 </el-button>
                </el-row>
            </div>
            <div v-if="data.type == 3">
                <el-row>
                    <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <el-button type="primary" plain @click="shi"> 是</el-button>
                <el-button type="primary"  plain @click="fou"> 否</el-button>
                </el-row>
            </div>
            <div v-if="data.type == 4">
                <el-row>
                    <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <el-button  type="primary" plain @click="qd"> 确定</el-button>
                <el-button  type="primary" plain @click="cxsc"> 重新生成</el-button>
                </el-row>
            </div>
            <div v-if="data.type == 5"> 
                <!-- //图片展示 -->
                <el-row>
                    <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <img id="msgImg" style="width: 50px; height: auto;" :src="data.img" />
                </el-row> 
                <!-- <div><canvas id="messageCanvans" width="50" height="50"></canvas></div> --> 
            </div>
            <div v-if="data.type == 6">
                
                <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <el-button style="background-color: #d4cec9;" v-for="item of data.arr" :key="item.index" type="primary" > <div class="overflow">{{ item }} </div></el-button>
                
            </div>
            <div v-if="data.type == 7">
                <el-row>
                    <el-avatar :src="Robot" style="background-color: #e9b75a;"></el-avatar>
                <el-button type="primary" plain @click="lj"> 逻辑 </el-button>
                <el-button type="primary" plain @click="dm"> 代码 </el-button>
                </el-row>
            </div>
        </div>
    </div>
    
</template>

<script>
import selfimg from '../../assets/1.png'
import store from '../../store'
import ET from '../../assets/icon/幼儿.svg'
import Robot from '../../assets/icon/机器人.svg'
import a1 from '../../assets/audio/1.mp3'
import a2 from '../../assets/audio/2.mp3'
import a3 from '../../assets/audio/3.mp3'
import a4 from '../../assets/audio/4.mp3'
import a5 from '../../assets/audio/5.mp3'
import a6 from '../../assets/audio/6.mp3'
export default {
    props: {
        data: {
            type: Object,
            default: () => {
                return {
                    self: false, //是否是自己
                    type: '',    //AI回复类型 0:普通回复 1:4选回复 2:手动或自动
                    content: '', //回复内容
                    arr: [],     //多选回复数组
                    img: '',     //图片
                    a1: a1,
                }
            }
        }
    },
    mounted() {
        if (this.data.self == false) {
            if (this.data.type == 0) {
                // console.log("speak")
                // let d = this.data.content
                // this.virtualClick(this.SpeakVoice, d)
                // document.getElementById('btnnn').dispatchEvent(new MouseEvent('click', { bubbles: true }))

                let audio = null
                console.log("audio")
                if (this.data.content.startsWith("我是小思老师")) {
                    // audio = new Audio(a1)
                } else if (this.data.content.startsWith("那么这个故事都有")) {
                    audio = new Audio(a2)
                } else if (this.data.content.startsWith("如果你喜欢可以为它添加一个图像")) {
                    audio = new Audio(a3)
                } else if (this.data.content.startsWith("如果你喜欢可以为它添加一个声音")) {
                    audio = new Audio(a4)
                } else if (this.data.content.startsWith("它会有哪些动作")) {
                    audio = new Audio(a5)
                } else if (this.data.content.startsWith("你觉得这个行为还需要细化吗")) {
                    audio = new Audio(a6)
                }
                if (audio != null) {
                    audio.play()
                }

            }
        }
        this.updateScroll()

    },
    data() {
        return {
            input: '',
            ET: ET,
            Robot: Robot,
        }
    },
    methods: {
        updateScroll() {
            console.log('message_updateScroll')
            var element = this.$store.state.element

            // var element = document.getElementById("msg_end");

            // element.scrollIntoViewIfNeeded()
            // element.scrollByLines(10000)
            // element.scrollTop = element.scrollHeight;
            // element.scroll({
            //     top: 10000,
            //     behavior: "smooth"
            // });

            // element.scrollTo(0, 100000)
            if (element) {
                element.scroll({
                    top: 100000000,
                    behavior: "smooth"
                });
            }

            // this.$nextTick(() => {
            //     this.$refs.message.scrollTop = this.$refs.message.scrollHeight
            // })
        },
        lj() {
            this.$store.state.state = 6;
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
        },
        dm() {
            this.$store.state.state = 5.3;
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
        },
        addObj(item) {
            this.$store.state.object = item;
            this.$store.state.state = 1.11;
            // this.$store.state.chatting_flag = 0;
            // this.$store.state.chatting_flag = 1;
            this.$store.state.jsmind_flag = 0;
            this.$store.state.jsmind_flag = 1;
        },
        qd() { //确定
            if (this.$store.state.state == 2.11) {
                this.$store.state.state = 3;
                this.$store.state.gSound = 1;
            } else if (this.$store.state.state == 3.31) {
                this.$store.state.state = 3.32;
            } else if (this.$store.state.state == 4.31) {
                this.$store.state.state = 4.32;
            } else if (this.$store.state.state == 5.31) {
                this.$store.state.state = 5.32;
            } else if (this.$store.state.state == 6.21) {
                this.$store.state.state = 6.22;
            }
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
            this.$store.state.jsmind_flag = 0;
            this.$store.state.jsmind_flag = 1;
        },
        cxsc() {//重新生成
            if (this.$store.state.state == 2.11) {
                this.$store.state.state = 2.1;
            } else if (this.$store.state.state == 3.31) {
                this.$store.state.state = 3.3;
            } else if (this.$store.state.state == 4.31) {
                this.$store.state.state = 4.3;
            } else if (this.$store.state.state == 5.31) {
                this.$store.state.state = 5.3;
            } else if (this.$store.state.state == 6.21) {
                this.$store.state.state = 6.2;
            }
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
            if (this.$store.state.state != 5.3) {
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;
            }
        },
        shi() {//是

            this.$store.state.state += 0.1;
            console.log('shi:' + this.$store.state.state)
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
            this.$store.state.jsmind_flag = 0;
            this.$store.state.jsmind_flag = 1;
        },
        fou() {//否
            if (this.$store.state.state == 4) {
                this.$store.state.state = -1;
            } else {
                this.$store.state.state += 1;
            }
            console.log('fou:' + this.$store.state.state)
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
        },
        sd() { //手动
            if (this.$store.state.state == 1) {
                this.$store.state.state = 1.1
            } else if (this.$store.state.state == 3.1) {
                this.$store.state.state = 3.2;
            } else if (this.$store.state.state == 4.1) {
                this.$store.state.state = 4.2;
            } else if (this.$store.state.state == 5.1) {
                this.$store.state.state = 5.2;
            } else if (this.$store.state.state == 6) {
                this.$store.state.state = 6.1;
            }

            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
            // console.log('sd:' + this.$store.state.state)
        },
        zd() { //自动
            if (this.$store.state.state == 1) {
                this.$store.state.state = 1.2
            } else if (this.$store.state.state == 3.1) {
                this.$store.state.state = 3.3;
            } else if (this.$store.state.state == 4.1) {
                this.$store.state.state = 4.3;
            } else if (this.$store.state.state == 5.1) {
                this.$store.state.state = 5.3;
            } else if (this.$store.state.state == 6) {
                this.$store.state.state = 6.2;
                this.$store.state.jsmind_flag = 0;
                this.$store.state.jsmind_flag = 1;
            }
            this.$store.state.chatting_flag = 0;
            this.$store.state.chatting_flag = 1;
            console.log('zd:' + this.$store.state.state)
        },
        hyp() { //换一批
            // this.$store.state.hyp = 1
            this.$store.state.state = 1.21
            this.$store.state.jsmind_flag = 0;
            this.$store.state.jsmind_flag = 1;
        },
        self_click() {
            console.log('self_click')
            let d = data.content
            this.virtualClick(this.SpeakVoice, d)

        },
        voice() {
            const reg = new window.webkitSpeechRecognition;
            // const reg = new window.SpeechRecognitionResult()
            reg.lang = 'zh-CN';
            console.log('voice')
            const that = this
            reg.onresult = function (event) {
                const content = event.results[0][0].transcript;
                console.log(`你说的是：${content}`);
                that.input += content;
            };
            reg.start()
            reg.end(3000)
        },
        SpeakVoice(msg = 'hellow world') {
            const speech = new SpeechSynthesisUtterance(msg)
            speech.rate = 0.8
            speech.pitch = 1.0
            // 设置兼容中文
            const voices = window.speechSynthesis.getVoices()
            speech.voice = voices.filter(function (voice) {
                return voice.localService == true && voice.lang == 'en-US'
            })[0]
            console.log(window.speechSynthesis)
            window.speechSynthesis.speak(speech)
        },

        /**
         * 语音播报 带延迟 异步
         * 搭配async await
         * @param msg 播报的信息
         */
        speakWithDelay(utterance, delay = 1000) {
            return new Promise(resolve => {
                const speech = new SpeechSynthesisUtterance(utterance)
                // 设置兼容中文
                let voices = window.speechSynthesis.getVoices()
                speech.voice = voices.filter(function (voice) {
                    return voice.localService == true && voice.lang == 'en-US'
                })[0]
                speech.onend = () => {
                    setTimeout(resolve, delay)
                }
                window.speechSynthesis.speak(speech)
            })
        },

        /**
         * 模拟按钮点击
         * @param callback
         */
        virtualClick(callback, msg) {
            let button = document.createElement('button')
            button.textContent = '点击我'
            // 添加点击事件处理程序
            button.addEventListener('click', function () {
                // console.log(callback)
                callback && callback(msg)
                // if (audio != null) {
                //     console.log('virtualclick')
                //     audio.play()
                // }
            })

            // 模拟用户点击事件
            let event = new MouseEvent('click', {
                view: window,
                bubbles: true,
                cancelable: true
            })
            button.dispatchEvent(event)
        },

        beQuiet() {
            console.log('停止')
            window.speechSynthesis.cancel()
            SpeakVoice('')
        },
    }
}
</script>

<style lang="less" scoped>
.overflow {
    white-space: normal !important;
    word-break: break-all;
    text-align: left;
}

.container {
    flex-direction: column;
    justify-content: center;
    align-content: center;
    align-items: center;
}

.all {
    margin-top: 30px;
    display: inline;
    // margin-bottom: 30px;
}

.self {
    float: right;
    // height: 80px;
    width: 80%;
    text-align: right;
}

.gpt {
    float: left;
    // height: 80px;
    width: 80%;
    text-align: left;
}

.other {
    text-align: left;
}
</style>